@import '../assets/styles/Vars.scss';

.App{
  display: grid;
  grid-template-columns: 390px 1fr;
  grid-template-rows: 60px 1fr;
  height: 100vh;
  width: 100vw;
  background-color: $background;
  .head{
    display: flex;
    justify-content: space-between;
    align-items: center;
    grid-column-start: 1;
    grid-column-end: 3;
    background-color: $blue;
    color: $white;
    padding: 10px 20px;
    .menu menu {
      display: flex;
    }
  }
  .left{
    background-color: $white;
    height: calc( 100vh - 60px );
  }
  .schedule{
    padding: 24px 20px;
    max-height: calc( 100vh - ( 60px + 460px ) );
    overflow: auto;
  }
  .content{
    padding: 20px 20px 80px 20px;
  }
}